}
.welcomeText {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 7rem;
  font-weight: 500;
  margin: 0;
  padding: 32px;
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 8px;
