  // align-items: center; /* align vertical */
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  // font-size: 300%;
}
.welcomeTextBackground {
  grid-column: 2 / 2;
  grid-row: 2 / 3;
  height: 100%;
  background-image: linear-gradient(
    to bottom left,
    #001529,
    rgb(103, 255, 230)
